export default {
	data() {
		return {
			one:[],
			refund:[],
			wuliu:[],
			id:''
		}
	},
	onLoad(e) {
		if(e.id){
			this.id = e.id;
		}
	},
	onShow() {
		uni.showLoading({
			title: '请稍后'
		})
		uni.request({
			url:uni.getStorageSync('hostname') + '?action=index/order/getShouhouDetail',
			data:{id:this.id,token:uni.getStorageSync('cbe_token')},
			method:'POST',
			dataType:'json',
			header: {'content-type' : 'application/x-www-form-urlencoded'},
			success: (resa) => {
				uni.hideLoading();
				var datas = resa.data;
				this.one = datas.data;
				this.refund = datas.refund;
				this.wuliu = datas.wllist;
			},
		})
	},
	methods:{
		goDelivery(ordersn){
			uni.navigateTo({
				url:'/pages/shouhou/wuliu?id='+this.refund.id+"&ordersn="+ordersn
			})
		},
		jihui(id){
			uni.navigateTo({
				url:'/pages/shouhou/jihui?id='+id
			})
		},
		quxiaoOrder(id){
			uni.showModal({
			    content: '确定取消申请吗？',
				confirmColor:'#008AB0',
			    success: (e)=>{
			    	if(e.confirm){
						uni.showLoading({
							title: '请稍后'
						})
						uni.request({
							url:uni.getStorageSync('hostname') + '?action=index/order/quxiao_shouhou',
							data:{id:id,token:uni.getStorageSync('cbe_token')},
							method:'POST',
							dataType:'json',
							header: {'content-type' : 'application/x-www-form-urlencoded'},
							success: (resa) => {
								uni.hideLoading();
								var one = resa.data;
								if(one.code==0){
									
								}
							}
						})
			    	}
			    }
			});
		}
	}
}
